@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600;700&display=swap");
@import "utilities";
@import "layouts";

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style-type: none;
  border-collapse: collapse;
}

html,
body {
  color: white;
  background-color: black;
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  h1 {
    @include media {
      font-size: 60px;
      line-height: 80px;
    }
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
  }
  h2 {
    @include media {
      font-size: 48px;
      line-height: 72px;
    }
    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
  }
  h3 {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
    @include media {
      font-size: 24px;
      line-height: 36px;
    }
  }
  p {
    font-size: 12px;
    line-height: 24px;
    @include media {
      font-size: 16px;
      line-height: 26px;
    }
    font-weight: 300;
  }
}

.section {
  overflow: hidden;
}

header#cover {
  position: relative;
  display: grid;
  place-items: center;
}

#invention {
  .cover {
    transition: transform .15s ease;
    display: none;
    @include media(720px) {
      display: initial;
      min-width: 96px;
      min-height: 96px;
    }
    @include media {
      width: 100%;
      height: 420px;
    }
  }
  .text-content {
    display: flex;
    flex-direction: column;
    gap: 0.125rem;
  }
  li.item {
    display: flex;
    flex: 1;
    flex-direction: row;
    min-height: 96px;
    align-items: center;
    gap: 2rem;
    @include media {
      flex-direction: column;
      align-items: flex-start;
    }
    &:hover {
      .cover {
        transform: scale(1.1)
      }
    }
  }
}

#logo {
  .evolution {
    height: 100%;
    width: 100%;
    background: #fff;
    display: grid;
    place-items: center;
    img {
      object-fit: contain;
      width: 100%;
    }
  }
}
